@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
/* Poppins font  */

body{
    font-family: "Poppins", sans-serif;
}

.cricket-teams-wrapper{
    padding-top: 80px;
    padding-bottom: 80px;
}



.selected-player-card img{
    width: 70px;
    float: left;
    border-radius: 50%;
}
.selected-player-card div{
    padding-left: 80px;
}
.show-info-box{
    position: fixed;
    top: 0;
    left:0;
    right:0;
    bottom:0;
    background-color: rgb(0 0 0/50%);
    display: grid;
    place-items: center;
    z-index: 99999;
}
.back-to-top {
  position: fixed;
  bottom: 1rem;
  right: 1.5rem;
}

